.App {
  min-height: 100vh;
  width: 100vw;
  background-color: rgb(21, 31, 43);
  padding: 30px;
}

.post {
  width: 1000px;
  height: 600px;
  margin: 0 auto;
  background-color: rgb(35,53,72);
  display: flex;
  flex-direction: column;
  color: white;
  border-radius: 20px;
}

.user_info {
  display: flex;
  margin: 20px 0 0 70px;
  align-items: center;
  justify-content: left;
  gap: 10px;
}

.userpic {
  width: 80px;
  height: 80px;
  border-radius: 50%;
}

.user_info > p {
  font-size: 16px;
  color: gray;
  margin-left: 15px;
}

.user_info > span {
  font-size: 16px;
  color: gray;
  margin-left: 10px;
}

.content {
  display: flex;
  flex-direction: column;
}

.content > p {
  margin: 30px 0 0 70px;
  font-size: 18px;
}

.post_img {
  transform: scale(0.9);
  border-radius: 20px;
  align-self: center;
}

.interaction {
  display: flex;
  align-self: center;
  gap: 200px;
  cursor: pointer;
}